<template>
    <div class="alert">
        <div class="alert-wrap" @click="childPlus">
            <div class="text" >
                {{alertMsg}}
                {{num}}
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    export default {
        props:{
           alertMsg:String,
           num: Number
        },
        data() {
            return {
                num2: 0
            }
        },
        methods: {
            childPlus() {
                // let 
                this.num2 = this.num;
                let numChild = this.num2 + 1;
                this.$emit('childByValue', numChild)
            }
        }
        // computed: mapState({
        //     alertMsg: state => state.com.alertMsg
        // })
    }
</script>
<style scoped>
   .alert {
        z-index: 1000 
    }
    .text {
             font-size: 14px;
            color: #fff;
            }
    .alert-wrap {
            position: fixed;
            width: 250px;
            height: 70px;
            line-height: 70px;
            text-align: center;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            border-radius: 10px;
            background: rgba(0, 0, 0, 0.6);
        }
</style>



